* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.form-title {
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 20px;
}

.form-input {
    padding: 10px 15px;
    display: block;
    margin-bottom: 15px;
    width: 100%;
    color: white;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid white;
    outline: none;
}

.form-container {
    color: white;
    padding: 30px 30px 0 30px;
}

.chat-container {
    display: flex;
    height: 80%;
    background-color: rgb(63, 63, 63);
    width: 90%;
    border-radius: 10px;
}

.chat-mainchat {
    flex: 2;
    background-color: rgb(96, 95, 95);
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.chat-sidebar {
    flex: 1;
}

.navbar-img {
    width: 40px;
    height: 40px;
    object-fit: contain;
    border-radius: 50%;
}

.navbar {
    display: flex;
    padding: 10px 10px;
    align-items: center;
    background-color: rgb(36, 36, 36);
    border-radius: 10px;
}

.navbar-logo {
    flex: 1 1 auto;
    font-size: 30px;
    color: blue;
}

.navbar-name {
    margin: 0 20px 0 7px;
}

.navbar-button {
    padding: 3px;
    border-radius: 10px;
    font-size: 12px;
    background-color: #ffffff95;
}

.search {
    border-bottom: 1px solid white;
}

.search-input {
    width: 100%;
    background-color: rgb(58, 58, 58);
    border: none;
    outline: none;
    padding: 8px;
    color: white;
}

.chats {
    background-color: rgb(58, 58, 58);
    height: 80%;
    overflow: hidden;
}

.user-img {
    width: 50px;
    height: 50px;
    object-fit: contain;
    border-radius: 50%;
}

.user {
    display: flex;
    align-items: center;
    padding: 10px;
}

.user:hover {
    background-color: rgb(108, 108, 108);
}

.user.active {
    background-color: rgb(153, 152, 152);
}

.user-info {
    margin-left: 10px;
}

.user-last-message {
    font-size: 12px;
    color: #ffffff95;
}

.input-img {
    width: 20px;
    height: 20px;
    object-fit: cover;
    background-color: white;
    cursor: pointer;
}

.messages {
    flex: 1 1 auto;
    padding: 10px 15px 0 10px;
}

.mainchat-input {
    display: flex;
    background-color: white;
    align-items: center;
    border-bottom: 1px solid rgb(96, 95, 95);
    border-right: 1px solid rgb(96, 95, 95);
    border-radius: 10px;
}

.input-text {
    padding: 20px;
    outline: none;
    border: none;
    flex: 1 1 auto;
}

.input-button {
    border: none;
    background-color: rgb(91, 91, 91);
    padding: 5px 10px;
    border-radius: 10px;
    margin-left: 10px;
}

.message {
    display: flex;
    margin-bottom: 10px;
}

.message-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: contain;
}

.message-info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.message-time {
    font-size: 11px;
    color: lightgray;
    font-style: italic;
}

.message-text {
    background-color: #fff;
    padding: 5px 10px;
    color: black;
    margin: 10px 10px;
    max-width: 400px;
    border-radius: 0 10px 10px 10px;
    display: inline-block;
}

.message-content img {
    width: 40%;
    margin: 0px 10px;
    display: block;
}

.message-owner {
    display: flex;
    margin-bottom: 10px;
    flex-direction: row-reverse;
}

.message-owner .message-content {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.message-owner .message-text {
    border-radius: 10px 0px 10px 10px;
    background-color: rgb(0, 191, 255);
    color: white;
}

.message-title {
    text-align: center;
}

.contextMenu {
    position: fixed;
    border-radius: 5px;
    padding: 10px 0;
    background-color: #fff;
    z-index: 2;
}

.contextMenuItem {
    color: black;
    cursor: pointer;
    padding: 2.5px 10px;
}

.contextMenuItem:hover {
    background-color: rgb(203, 202, 202);
}

ul[class],
ol[class] {
    list-style: none;
}